import WebViewer from '@pdftron/webviewer';
import React, { useEffect, useRef } from 'react';

const pdftronLicenseKey = "";

interface Props {
    file: File
}

export const DocumentViewer: React.FC<Props> = (props) => {
    const viewerRef = useRef(null);
    const webviewerInstance = useRef<any>();

    useEffect(() => {
        WebViewer(
            {
                path: "/pdftron/",
                licenseKey: pdftronLicenseKey,
                disabledElements: ["header", "toolsHeader"],
                fullAPI: true,
                initialDoc: "/sample.pdf",
            },
            viewerRef.current!
        ).then((instance) => {
            webviewerInstance.current = instance
        });
    }, []);

    useEffect(() => {
        if(webviewerInstance.current){
            webviewerInstance.current.UI.loadDocument(props.file)
        }
    }, [props.file])

    return (
        <div ref={viewerRef} style={{ height: "80vh", marginTop: "10px" }}></div>
    )
}
